
import { defineComponent } from "vue";
import VideoGameService from '../service/VideoGameService';


export default defineComponent({
  name: "Home",
  data() {
    return {
      videoGameService: new VideoGameService(),
      games: null,
      responsiveOptions: [
        {
          breakpoint: '1024px',
          numVisible: 3,
          numScroll: 3
        },
        {
          breakpoint: '600px',
          numVisible: 2,
          numScroll: 2
        },
        {
          breakpoint: '480px',
          numVisible: 1,
          numScroll: 1
        }
      ],
      displayDialog: false,
    }
  },
  methods: {
    openDialog() {
      this.displayDialog = true;
    },
  },
  created() {
    this.videoGameService = new VideoGameService();
  },
  mounted() {
    this.videoGameService.getVideoGames().then(data => this.games = data.slice(0,9));
  },
});
